<template>
    <div>
        <van-nav-bar title="" left-text="" left-arrow @click-left="onClickLeft" />
        <!--  -->
        <van-field readonly clickable name="picker" :value="value" label="" placeholder=""
            @click="showPicker = true" style="margin-top: 10px;" />
        <van-popup v-model="showPicker" position="bottom">
            <van-picker show-toolbar :columns="columns" @confirm="onConfirm" @cancel="showPicker = false" />
        </van-popup>
        <!--  -->
        <van-field readonly clickable name="picker" :value="values" label="" placeholder=""
            @click="showArea = true" />
        <van-popup v-model="showArea" position="bottom">
            <van-picker show-toolbar :columns="areaList" @confirm="onConfirmb" @cancel="showArea = false" />
        </van-popup>
        <!--  -->
        <wd-input v-model="value1" placeholder="" label="()"></wd-input>
        <!--  -->
        <wd-datetime-picker type="date" v-model="value2" label=""></wd-datetime-picker>
        <!--  -->
        <wd-input v-model="value3" placeholder="" label=""></wd-input>
        <!--  -->
        <wd-input v-model="value4" placeholder="" label=""></wd-input>
        <!--  -->
        <!-- <wd-input v-model="value5" placeholder="" label=""></wd-input> -->
        <!--  -->
        <div
            style="text-align: left;border-top: 0.1px solid grey; padding-top: 10px; background-color: #fff; position: relative;">
            <h3></h3>
            <span style="position: absolute; right: 0; top: 30px;"></span>
            <wd-upload v-model="fileList" multiple action="https://jsonplaceholder.typicode.com/posts/"></wd-upload>
        </div>
        <!--  -->
        <div style="border-top: 0.1px solid grey;  background-color: #fff;">
            <h4 style="display: inline-block;"></h4>
            <wd-input type="textarea" v-model="value6" rows="6" maxlength="10" clearable show-word-limit
                placeholder=""></wd-input>
        </div>
        <!--  -->
        <wd-button type="warning" block style="margin-top: 5px;" @click="submitFoundItem"></wd-button>
    </div>
</template>

<script>

export default {
    name: 'Shiwu',
    data() {
        return {
            value: '',
            values: '',
            value1: '',
            value2: '',
            value3: '',
            value4: '',
            value5: '',
            value6: '',
            columns: ['', '', '', '', '', '', '', '', '', ''],
            showPicker: false,
            showArea: false,
            areaList: ['', '',],
            fileList: [{
                name: '',
                url: 'https://img0.baidu.com/it/u=872626013,2257756031&fm=253&fmt=auto&app=138&f=JPEG?w=750&h=500'
            }],
        }
    },
   
    methods: {
        onClickLeft() {
            this.$router.push('/');
        },
        onConfirm(value) {
            this.value = value;
            this.showPicker = false;
        },
        onConfirmb(values) {
            this.values = values;
            this.showArea = false;
        },
        submitFoundItem() {
            console.log('Submitting found item', this.value);
            const newItem = {
                type: this.value,
                area: this.values,
                detailedLocation: this.value1,
                lostDate: this.value2,
                contact: this.value3,
                publisherName: this.value4,
                description: this.value6,
                image: this.fileList.map(file => file.url)
            };
            this.$store.dispatch('addFoundItem', newItem).then(() => {
                this.$router.push('/hall');
            }).catch(error => {
                console.error('Error adding item:', error);
            });
        }

    },
}
</script>

<style></style>


