import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Pay from '@/components/Pay'
import Hall from '@/components/Hall'
import MyLogin from '@/components/MyLogin'
import Shiwu from '@/components/Shiwu'
import Xunwu from '@/components/Xunwu'
import Customer from '@/components/Customer'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld
    },
    {
      path: '/Pay',
      name: 'Pay',
      component: Pay
    },
    {
      path: '/Hall',
      name: 'Hall',
      component: Hall
    },
    {
      path: '/MyLogin',
      name: 'MyLogin',
      component: MyLogin
    },
    {
      path: '/Shiwu',
      name: 'Shiwu',
      component: Shiwu
    },
    {
      path: '/Xunwu',
      name: 'Xunwu',
      component: Xunwu
    },
    {
      path: '/Customer',
      name: 'Customer',
      component: Customer
    },
  ]
})



