<template>
  <div class="full-background">
    <img src="@/assets/bg2.png" alt="Background Image" class="background-image">
    <div class="content">
      <h1 style="text-align: center; font-size: 35px;"></h1>
      <el-tabs v-model="activeName">
        <van-search v-model="value" placeholder="" background="#e1f6fe" />
        <el-tab-pane label="" name="first">
          <wd-status-tip type="collect" tip="" v-show="foundItems.length === 0" />
          <div class="container" @click="confirmg">
            <div class="item" v-for="(foundItem, index) in foundItems" :key="index">
              <img :src="foundItem.image" alt="Found Item Image">
              <div style="display: flex; align-items: center;">
                <span
                  style="width: 25px; height: 20px; background-color: red; border-radius: 50%; color: #fff; text-align: center;"></span>
                <p>&nbsp;&nbsp;{{ foundItem.description }}</p>
              </div>
            </div>
          </div>

        </el-tab-pane>
        <el-tab-pane label="" name="second">
          <wd-status-tip type="collect" tip="" v-show="foundItems.length === 0"/>
          <div class="container" @click="confirmg">
            <div class="item" v-for="(item, index) in items" :key="index">
              <img :src="item.image" alt="Item Image">
              <div style="display: flex; align-items: center;">
                <span
                  style="width: 25px; height: 20px; background-color: red; border-radius: 50%; color: #fff; text-align: center;"></span>
                <p>&nbsp;&nbsp;{{ item.details }}</p>
              </div>
            </div>
          </div>
        </el-tab-pane>
        <el-tab-pane label="" name="third">
          <wd-status-tip type="content" tip="" />
        </el-tab-pane>
        <el-tab-pane label="" name="fourth">
          <!-- <wd-status-tip type="content" tip="" /> -->
          <div class="container">
            <div class="item">
              <img src="../assets/cat.png" alt="Left Image">
              <div style="display: flex; align-items: center;">
                <span
                  style="width: 25px; height: 20px; background-color: red; border-radius: 50%; color: #fff; text-align: center;"></span>
                <p>&nbsp;&nbsp;</p>
              </div>
            </div>
            <div class="item">
              <img src="../assets/rabbit.png" alt="Right Image">
              <div style="display: flex; align-items: center;">
                <span
                  style="width: 25px; height: 20px; background-color: red; border-radius: 50%; color: #fff; text-align: center;"></span>
                <p>&nbsp;&nbsp;</p>
              </div>
            </div>
          </div>
        </el-tab-pane>
      </el-tabs>
      <div class="footer">
        <van-tabbar v-model="active">
          <van-tabbar-item icon="home-o" to="/"></van-tabbar-item>
          <van-tabbar-item icon="like-o" to="/Pay"></van-tabbar-item>
          <van-tabbar-item icon="comment-o " to="/Hall"></van-tabbar-item>
          <van-tabbar-item icon="manager-o" to="MyLogin"></van-tabbar-item>
        </van-tabbar>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  name: "Hall",
  data() {
    return {
      activeName: 'first',
      active: 2,
      searchText: '',
      books: [],
      filteredBooks: [],
      search: '',
      value: '',
    }
  },
  computed: {
    items() {
      return this.$store.state.items;
    },
    foundItems() {
      return this.$store.state.foundItems;
    },

  },
  methods: {
    confirmg () {
      this.$messageBox.confirm('', '').then(() => {
        this.$messageBox.alert(',')
      })
    }
  }
};

</script>

<style scoped>
.background-image {
  position: absolute;
  top: -13%;
  width: 390px;
  /*  */
  height: 50%;
  /*  */
  z-index: -1;
  /*  */
}

.content {
  position: relative;
  z-index: 1;
  /*  */
  padding-top: 38px;
  /*  */
}

.el-tabs__nav-wrap::after {
  background-color: #e1f6fe;
}

/* .el-tabs--top .el-tabs__item.is-top:nth-child(2) {
  padding-left: 25px;
} */

.container {
  display: flex;
  justify-content: space-between;
}

.item {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 50%;
}

.item img {
  width: 70%;
}

.item p {
  display: inline;
  font-weight: bold;
  font-size: 13px;
  word-wrap: break-word;
  /*  */
  overflow-wrap: break-word;
  /*  */
  text-align: justify;
  /*  */

}
</style>


