<template>
  <div>
    <van-nav-bar title="" left-text="" left-arrow @click-left="onClickLeft" />
    <div class="chatAppBody">
      <div class="chatBox">
        <!--  -->
        <div v-for="(message, index) in messages" :key="index"
          :class="message.sender === 'customer' ? 'chatRow chatRowMe' : 'chatRow'">
          <el-avatar class="chatAvatar" :size="30" src="https://img2.baidu.com/it/u=3987897595,2066434013&fm=253&fmt=auto&app=120&f=JPEG?w=800&h=1067"
            v-if="message.sender !== 'customer'"></el-avatar>
          <div class="chatMsgContent">
            <div class="chatUsername">{{ message.sender === 'customer' ? '' : '' }}</div>
            <div class="chatContent">{{ message.content }}</div>
          </div>
        </div>
      </div>
      <div class="chatBottom">
        <div class="chatInputArea">
          <!--  -->
          <textarea v-model="newMessage" class="chatInput" placeholder="..."></textarea>
          <!--  -->
          <button class="chatSendButton" @click="sendMessage"></button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Customer',
  data() {
    return {
      newMessage: '',
      messages: []
    };
  },
  methods: {
    onClickLeft() {
      this.$router.push('/');
    },
    sendMessage() {
      // 
      if (this.newMessage.trim() !== '') {
        this.messages.push({ sender: 'customer', content: this.newMessage.trim() });
        // 
        // 
        // 
        this.newMessage = '';
        // 
        this.sendFixedResponse();
      }
    },
    sendFixedResponse() {
      // 
      const fixedResponse = '';
      this.messages.push({ sender: 'server', content: fixedResponse });
      // 
      // 
    }
  },
}
</script>
<style scoped>
.chatAppBody {
  display: flex;
  flex-direction: column;
  height: 100vh;
  background-color: #f1f5f8;
}

.chatTitle {
  background: #fff;
}

.chatBox {
  flex: 1;
  padding: 0 5px;
}

.chatBottom {
  background: #fff;
}

.chatRow {
  display: flex;
  align-items: flex-end;
  margin: 5px 0px;
}

.chatAvatar {
  margin-right: 5px;
  flex-shrink: 0;
}

.chatUsername {
  font-size: 12px;
  white-space: nowrap;
  color: #999;
  margin-bottom: 2px;
}

.chatContent {
  border-radius: 10px 10px 10px 0px;
  padding: 10px;
  background-color: rgb(255, 255, 255);
  box-shadow: 0 5px 30px rgb(50 50 93 / 8%), 0 1px 3px rgb(0 0 0 / 5%);
  font-size: 14px;
  word-break: break-all;
  line-height: 21px;
}

.chatRowMe {
  justify-content: flex-end;
}

.chatRowMe .chatContent {
  border-radius: 10px 10px 0px 10px;
}

.chatInputArea {
  display: flex;
  align-items: center;
  padding: 10px;
  border-top: 1px solid #ddd;
  background-color: #f5f5f5;
}

.chatInput {
  flex: 1;
  padding: 5px 10px;
  border: none;
  border-radius: 5px;
  resize: none;
  outline: none;
  font-size: 14px;
}

.chatSendButton {
  margin-left: 10px;
  padding: 5px 15px;
  background-color: #4CAF50;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.chatSendButton:hover {
  background-color: #45a049;
}
</style>


