<template>
  <div>
    <div style="display: flex; align-items: center; margin: 30px 40px;">
      <van-image src="https://img1.baidu.com/it/u=2101556152,538604057&fm=253&fmt=auto&app=138&f=JPEG?w=500&h=500" round
        width="5.5rem" height="5.5rem">
        <template v-slot:error></template>
      </van-image>
      <span style="margin-left: 20px; font-weight: bold; font-size: 20px;">{{ username }}</span>
      <wd-button type="icon" icon="wd-icon-edit-outline" @click="confirmq"></wd-button>
    </div>
    <div>
      <wd-notice-bar text="''," prefix="warn-bold" type="danger"
        style="font-size: 15px;"></wd-notice-bar>
    </div>
    <div class="text_gr" style="margin-top: 15px;">
      <wd-card style="box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2); transition: 0.3s;  position: relative;">
        <div
          style="background-color: #FFFFFF; border-radius: 10px;  margin: 5px; display: inline-block; text-align: center; padding-left: 20px;">
          <van-icon name="friends-o" size="40" color="#1989fa" style="margin-left: 5px;"></van-icon>
          <h2 style="color: black; margin: 0; margin-left: 5px;"></h2>
          <span style="font-weight: bold; font-size: 15px;" @click="confirms">{{ msg }}</span>
        </div>
        <div
          style=" background-color: #FFFFFF; border-radius: 10px;  margin: 5px; position: absolute; right: 0; top:20%; padding-right: 40px; display: flex; flex-direction: column; align-items: center; text-align: center;">
          <van-icon name="phone-o" size="40" color="#1989fa" style="margin-left: 10px;"></van-icon>
          <h2 style="color: black; margin: 0;"></h2>
          <span style=" font-weight: bold; font-size: 15px;" @click="confirm">{{ msgs }}</span>
        </div>
      </wd-card>
    </div>

    <div style="margin-top: 20px;">
      <wd-card title=" >">
        <!--  -->
        <div class="item">
          <img src="../assets/cat.png" alt="">
          <div class="details">
            <p style="color: black; font-weight: bold;"></p>
            <p style="color: black;">:2</p>
            <p style="font-weight: bold;"></p>
          </div>
          <!-- <button></button> -->
        </div>
        <div class="item">
          <img src="../assets/bird.png" alt="">
          <div class="details">
            <p style="color: black; font-weight: bold;"></p>
            <p style="color: black;">,</p>
            <p style="font-weight: bold;"></p>
          </div>
          <!-- <button></button> -->
        </div>
      </wd-card>
    </div>
    <div class="footer">
      <van-tabbar v-model="active">
        <van-tabbar-item icon="home-o" to="/"></van-tabbar-item>
        <van-tabbar-item icon="like-o" to="/Pay"></van-tabbar-item>
        <van-tabbar-item icon="comment-o " to="/Hall"></van-tabbar-item>
        <van-tabbar-item icon="manager-o" to="MyLogin"></van-tabbar-item>
      </van-tabbar>
    </div>
  </div>
</template>

<script>
export default {
  name: "MyLogin",
  data() {
    return {
      active: 3,
      msg: "",
      msgs: "",
      personName: '',
      isShow: false, // 
      username: '012345', // 
    }
  },
  methods: {
    confirm() {
      this.$messageBox.prompt('', {
        inputPattern: /.+@.+\..+/i
      }).then(({ value }) => {
        this.$messageBox.alert(` ${value}`),
          this.msgs = value
      })
    },
    confirms() {
      this.$messageBox.prompt('', {
        inputPattern: /^[\u4e00-\u9fa5]+$/,
        inputErrorMessage: ''
      }).then(({ value }) => {
        this.$messageBox.alert(` ${value}`),
          this.msg = value
      })
    },
    showMessageBox() {
      this.isShow = true;
    },
    handleConfirm() {
      this.isShow = false;
      // 
      console.log("Confirmed username:", this.username);
    },
    handleCancel() {
      this.isShow = false;
      console.log("Username input was cancelled.");
    },
    confirmq() {
      this.$messageBox.prompt('', {
        inputPattern: /\S+/  // 
      }).then(({ value }) => {
        if (value) {  // 
          this.username = value;  //  data 
          this.$messageBox.alert(`${value}`);
        } else {
          this.$messageBox.alert('');
        }
      }).catch(() => {
        this.$messageBox.alert('');
      });
    }
  }
}
</script>

<style scoped>
.item {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}

img {
  max-width: 100px;
  margin-right: 20px;
}

.details {
  flex-grow: 1;
}
</style>


